function HashRate(props) {
    if (props.actual === props.target) {
        // 酒红色：头图最亮部分 #c13b44
        return <span className="staking-number" style={{ fontWeight: 'bold', color: '#c13b44' }}>{props.target} %</span>;
    } else {
        return <span className="staking-number">{props.target} %</span>;
    }
}
function calcAnnual(myShare, totalShare, dailyQuota, staked) {
    if (dailyQuota === 0 || totalShare === 0 || myShare === 0) {
        return 0;
    }
    // console.log("dailyQuota: %d, myShare: %d, totalShare: %d", dailyQuota, myShare, totalShare);
    let dailyInterest = dailyQuota * myShare / totalShare;
    // console.log("dailyInterest: %d, staked: %d", dailyInterest, staked);
    return annual(dailyInterest, staked);
}

function annual(dailyInterest, principal) {
    if (principal === 0) {
        return 0;
    }
    return Math.pow(1 + dailyInterest / principal, 365) - 1;
}

export default Staking;