const SERO_CURRENCY = "SERO";

const contract = serojs.callContract(ABI, ADDR);

class Aggregator {
    callMethod(_method, mainPkr, _args, callback) {
        console.log("callMethod: args:", _args);
        let packData = contract.packData(_method, _args);
        let callParams = {
            from: mainPkr,
            to: ADDR,
            data: packData
        }

        seropp.call(callParams, function (callData) {
            if (callData !== "0x") {
                let res = contract.unPackData(_method, callData);
                if (callback) {
                    callback(res);
                }
            } else {
                callback("0x0");
            }
        });
    }

    executeMethod(_method, fromPK, mainPKr, args, cy, value, callback) {
        console.log("args: ", args);
        let packData = contract.packData(_method, args);
        let executeData = {
            from: fromPK,
            to: ADDR,
            value: "0x" + value.toString(16),//SERO
            data: packData,
            gasPrice: "0x" + new BigNumber("1000000000").toString(16),
            cy: cy,
        };
        let estimateParam = {
            from: mainPKr,
            to: ADDR,
            value: "0x" + value.toString(16),//SERO
            data: packData,
            gasPrice: "0x" + new BigNumber("1000000000").toString(16),
            cy: cy,
        }
        seropp.estimateGas(estimateParam, function (gas) {
            executeData["gas"] = gas;
            seropp.executeContract(executeData, function (res) {
                if (callback) {
                    callback(res)
                }
            })
        });
    }

    trigger(fromPK, mainPKr, poolAddr, daoAddr, callback) {
        console.log("multi-trigger");
        this.executeMethod("trigger", fromPK, mainPKr, [poolAddr, daoAddr], SERO_CURRENCY, 0, callback);
    }

    async poolStatus(mainPKr, poolAddr, callback) {
        console.log("poolStatus called");
        this.callMethod("poolStatus", mainPKr, [poolAddr], callback);
    }
    async poolInfo(mainPKr, poolAddr, callback) {
        console.log("poolInfo");
        this.callMethod("poolInfo", mainPKr, [poolAddr], callback);
    }
    async daoStatus(mainPKr, daoAddr, callback) {
        console.log("daoStatus");
        this.callMethod("daoStatus", mainPKr, [daoAddr], callback);
    }
    async daoInfo(mainPKr, daoAddr, callback) {
        console.log("daoInfo");
        this.callMethod("daoInfo", mainPKr, [daoAddr], callback);
    }

}

const aggregator = new Aggregator();
export default aggregator;