// v3
const ADDR = "2PwwseCxE4sRL1hatsuwkwv1gkYdBhnkpVTBNNPhXdcG7gkzXWcTWaEGwm6Xw4rzWs1CbaQCYEQrTpU5vqpHr893";

const SERO_CURRENCY = "SERO";
const MIDAFI_CURRENCY = "MONEY";

const contract = serojs.callContract(ABI, ADDR);
console.log("DemoStaking contract:", contract);

class DemoStaking {
    callMethod(_method, mainPkr, _args, callback) {
        console.log(_method, _args);
        let packData = contract.packData(_method, _args);
        let callParams = {
            from: mainPkr,
            to: ADDR,
            data: packData
        }

        seropp.call(callParams, function (callData) {
            if (callData !== "0x") {
                let res = contract.unPackData(_method, callData);
                if (callback) {
                    callback(res);
                }
            } else {
                callback("0x0");
            }
        });
    }

    executeMethod(_method, fromPK, mainPKr, args, cy, value, callback) {
        console.log("args: ", args);
        let packData = contract.packData(_method, args);
        let executeData = {
            from: fromPK,
            to: ADDR,
            value: "0x" + value.toString(16),//SERO
            data: packData,
            gasPrice: "0x" + new BigNumber("1000000000").toString(16),
            cy: cy,
        };
        let estimateParam = {
            from: mainPKr,
            to: ADDR,
            value: "0x" + value.toString(16),//SERO
            data: packData,
            gasPrice: "0x" + new BigNumber("1000000000").toString(16),
            cy: cy,
        }
        seropp.estimateGas(estimateParam, function (gas) {
            executeData["gas"] = gas;
            seropp.executeContract(executeData, function (res) {
                if (callback) {
                    callback(res)
                }
            })
        });
    }

    deposit(fromPK, mainPKr, amount, callback) {
        console.log("will deposit ", amount);
        this.executeMethod("deposit", fromPK, mainPKr, [], MIDAFI_CURRENCY, amount, callback);
    }
    send(fromPK, mainPKr, currency, amount, callback) {
        this.executeMethod("send", fromPK, mainPKr, [currency, amount.toString()], SERO_CURRENCY, 0, callback);
    }
}
const demoStaking = new DemoStaking();
export default demoStaking;